.commits {
    background: #F5F6F7;
    color: #1A1B22;
    font-style: normal;
    padding: 72px 0 65px;
    position: relative;
}

@media screen and (max-width: 768px) {
    .commits {
        padding: 33px 40px 26px;
    }
}

@media screen and (max-width: 375px) {
    .commits {
        padding: 32px 16px;
    }
}