.about-me {
    display: flex;
    padding: 80px 104px
}

@media screen and (max-width: 1100px) {
    .about-me {
        padding: 40px;
    }
}

@media screen and (max-width: 900px) {
    .about-me {
        align-items: center;
        padding: 40px;
    }
}

@media screen and (max-width: 700px) {
    .about-me {
        padding: 32px 16px;
        flex-direction: column;
    }
}