.header {
    height: 80px;
    width: 100%;
    font-style: normal;
    line-height: 24px;
    display: flex;
    justify-content: space-between;
    box-shadow: inset 0 -1px 0 #D1D2D6;
    box-sizing: border-box;
}

@media screen and (max-width: 768px) {
    .header {
        height: 66px;
    }
}

@media screen and (max-width: 420px) {
    .header {
        height: 58px;
    }
}