.footer {
    display: flex;
    justify-content: space-between;
    padding: 33px 104px 25px;
}

@media screen and (max-width: 940px) {
    .footer {
        padding: 21px 40px;
    }
}

@media screen and (max-width: 730px) {
    .footer {
        padding: 20px 16px;
        flex-wrap: wrap;
    }
}