.footer__menu {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

@media screen and (max-width: 600px) {
    .footer__menu {
        flex-direction: column;
        height: 120px;
        justify-content: space-between;
        margin: 0;
        padding: 0;
    }
}